<template>
  <div id="app">
	<el-row type="flex" justify="center">
		<el-row style="width: 393px;">
			<el-row style="margin: 15px;">
				<el-row  type="flex" align="middle">
					<el-row style="width: 80%" type="flex" align="middle">
						<img height="50px" width="50px" src="https://cdn.jsdelivr.net/gh/cpncoin/pncoin@main/node2/pn.png" />
						<h2 style="margin-left: 10px;">Polar Network</h2>
					</el-row>
					<el-row type="flex" justify="end">
						<el-dropdown @command="handleCommand">
							<span class="el-dropdown-link">
							{{lang}}<i class="el-icon-arrow-down el-icon--right"></i>
							</span>
							<el-dropdown-menu slot="dropdown">
								<el-dropdown-item command="en" >EN</el-dropdown-item>
								<el-dropdown-item command="zh" >中国</el-dropdown-item>
							</el-dropdown-menu>
						</el-dropdown>
					</el-row>
				</el-row>
			</el-row>
			<router-view></router-view>
		</el-row>
	</el-row>
  </div>
</template>

<script>


export default {
	data: function() {
		return {
			lang:"EN"
		}
	},
	created(){
		this.language.lang = this.$cookies.get("PN_language")
		if(this.language.lang=="zh"){
			this.lang = "中国"
		}else{
			this.language.lang = "en"
			this.lang = "EN"
		}
	},
	methods: {
		handleCommand(command) {
			if(command=="zh"){
				this.language.lang = "zh"
				this.lang = "中国"
				this.$cookies.set("PN_language","zh",Date.parse(new Date())/1000+60*60*24*90)
			}else if(command=="en"){
				this.language.lang = "en"
				this.lang = "EN"
				this.$cookies.set("PN_language","en",Date.parse(new Date())/1000+60*60*24*90)
			}
		},
	}
}
</script>

<style>
body{
  background: #f2f9f5;
  margin: 0px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>
