<script>
	export default {
		data: function() {
			return {
				cookie:"",
				address:"",
				ref:"",
				myref:"",
				input_address:"",
				d:"0",
				h:"0",
				m:"0",
				s:"0",
				Participant:"0",
				PN:"0"
			}
		},
		created() {
			if(this.$route.query.ref!=null){
				this.ref = this.$route.query.ref
				console.log("yes")
			}
			var address = this.$cookies.get("address")
			if(address != null){
				this.a1(address)
			}
			this.c()
		},
		methods: {
			Submit(){
				console.log(this.$route.query)
				this.a1(this.input_address)
			},
			a1(address){
				var temp = this
				this.$.ajax({url:"/airdrop/",data:{act:"apply",address:address,ref:this.ref,md5:this.$md5(address)},type:"post",dataType:"json",success:function(result){
					if(result.code == 200){
						temp.PN = result.data.PN
						temp.Participant = result.data.participant
						temp.myref = result.data.ref
						temp.address = result.data.address
						$cookies.set("address",result.data.address,Date.parse(new Date())/1000+60*60*24*90)
					}else{
						temp.$message.error(result.msg[temp.language.lang]);
					}
				}});
			},
			copy_(){
				this.copy("https://airdrop.pncoin.one/#/?ref="+this.myref)
				this.$message({
					message: this.language[this.language.lang].copy,
					type: 'success'
				});
			},
			copy(content){
				var aux = document.createElement("input"); 
				aux.setAttribute("value", content); 
				document.body.appendChild(aux); 
				aux.select();
				document.execCommand("copy"); 
				document.body.removeChild(aux);
			},
			c(){
				var temp = this
				var end_ts=1627790400;
				var prev_days=0;
				var prev_hours=0;
				var prev_minutes=0;
				var prev_seconds=0;
				var interval=setInterval(function(){
					var now_ts=Math.floor(new Date().getTime()/1000);
					var ts_diff=end_ts-now_ts;
					if(ts_diff<0){
						clearInterval(interval);
						location.reload();
						return;
					}
					var days=Math.floor(ts_diff/60/60/24);
					var hours=Math.floor((ts_diff-days*60*60*24)/60/60);
					var minutes=Math.floor((ts_diff-days*60*60*24-hours*60*60)/60);
					var seconds=Math.floor(ts_diff-days*60*60*24-hours*60*60-minutes*60);
					if(prev_days!=days){
						temp.d=days
					}
					if(prev_hours!=hours){
						temp.h=hours
					}
					if(prev_minutes!=minutes){
						temp.m=minutes
					}
					temp.s=seconds
					prev_days=days;
					prev_hours=hours;
					prev_minutes=minutes;
					prev_seconds=seconds;
				},1000);
			}
		}
	}
</script>
